.tui-pagination {
  font-family: 'Source Sans Pro', sans-serif;
  line-height: 1.5;
  letter-spacing: 0.007em;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 1rem 0;
}
@media screen and (max-width: 768px) {
  .tui-pagination {
    flex-direction: column-reverse;
    justify-content: inherit;
    align-items: center;
  }
}
.tui-pagination__text {
  display: inline-block;
}
.tui-pagination__selector {
  display: inline-block;
  margin-left: 2rem;
}
@media screen and (max-width: 768px) {
  .tui-pagination__selector {
    margin-bottom: 1rem;
    margin-left: 0;
  }
}
.tui-pagination__selector .tui-dropdown .oui-button {
  padding-right: 2rem;
}
.tui-pagination__selector .oui-button,
.tui-pagination__selector .oui-button-group,
.tui-pagination__selector .tui-dropdown {
  margin-bottom: 0;
}
.tui-pagination__size-selector {
  display: inline-block;
}
.tui-pagination__size-selector-text {
  text-decoration: underline;
  cursor: pointer;
}
.tui-dropdown {
  display: inline-block;
  position: relative;
}
.tui-dropdown .oui-button {
  position: relative;
}
.tui-dropdown .oui-button .oui-icon {
  position: absolute;
  top: 1px;
  right: 1px;
  bottom: 1px;
  width: calc(1rem * 2);
  height: auto;
  border-radius: calc(3px - 1px);
  margin: auto;
  background: #fff;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  pointer-events: none;
}
.tui-dropdown .oui-button .oui-icon::before {
  display: inline-block;
  color: #59697d;
  font-size: 1rem;
}
.tui-dropdown-menu {
  display: none;
  position: absolute;
  z-index: 100;
  bottom: calc(100% + 1rem);
  right: 0;
  background-color: #FFF;
  color: #000;
  border: 1px #EAEAEA solid;
  min-width: 100%;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
}
.tui-dropdown-menu_visible {
  display: block;
}
.tui-dropdown-menu::before,
.tui-dropdown-menu::after {
  content: " ";
  position: absolute;
  width: 0;
  height: 0;
  border: solid transparent;
  pointer-events: none;
  top: 100%;
  right: 15px;
}
.tui-dropdown-menu::before {
  border-width: 9px;
  margin: 0 -9px;
  border-top-color: #EAEAEA;
}
.tui-dropdown-menu::after {
  border-width: 7px;
  margin: 0 -7px;
  border-top-color: #fff;
}
.tui-dropdown-menu__content {
  overflow-x: hidden;
  overflow-y: scroll;
  margin: 0;
  padding: 0;
  max-height: 18rem;
  min-height: 10rem;
}
.tui-dropdown-menu__item,
.tui-dropdown-menu__group {
  display: block;
  list-style: none;
  text-align: left;
}
.tui-dropdown-menu__item .oui-button,
.tui-dropdown-menu__group .oui-button {
  margin-bottom: 0;
  background: none;
  width: 100%;
  text-align: left;
  font-weight: 400;
  border-radius: 0;
  padding: 0.6rem 1rem;
}
.tui-dropdown-menu__item .oui-button:hover,
.tui-dropdown-menu__group .oui-button:hover,
.tui-dropdown-menu__item .oui-button:focus,
.tui-dropdown-menu__group .oui-button:focus {
  background-color: #EAEAEA;
}
.tui-dropdown-menu__item .oui-button_selected,
.tui-dropdown-menu__group .oui-button_selected {
  font-weight: 600;
}
.tui-dropdown-menu__group {
  color: #595959;
  font-variant: small-caps;
  white-space: nowrap;
  padding: 0.6rem 1rem;
}



